.ChordStrip {
    background: yellow;
    color: #000;

    font-weight: bold;
    
    text-align: center;
    padding: 10px 15px;
    margin: 15px 0;
    
    cursor: pointer;
    
    border-radius: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;  
    
    display: inline-block;
}

.ChordStrip--active {
    -webkit-box-shadow: 0 0 40px #ffff00;
            box-shadow: 0 0 40px #ffff00;
    background:#ffff8e;
}

.ChordStrip:hover {
    -webkit-animation: neon .5s ease-in-out infinite alternate;
            animation: neon .5s ease-in-out infinite alternate;
}

@-webkit-keyframes neon {
    0% {
        -webkit-box-shadow: 0 0 15px #ffff00;
                box-shadow: 0 0 15px #ffff00;
        background: yellow;
    }
    100% {
        -webkit-box-shadow: 0 0 40px yellow;
                box-shadow: 0 0 40px yellow;
        background:#ffff8e;

    }
}

@keyframes neon {
    0% {
        -webkit-box-shadow: 0 0 15px #ffff00;
                box-shadow: 0 0 15px #ffff00;
        background: yellow;
    }
    100% {
        -webkit-box-shadow: 0 0 40px yellow;
                box-shadow: 0 0 40px yellow;
        background:#ffff8e;

    }
}


